.titleContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 150px;
    padding: 25px 40px;
    width: 100%;
    background-color: var(--grey-lighter);
    transition: background-color 0.3s;
}

.title {
    margin: 0;
    font-size: 16px;
    font-weight: 700;
    line-height: 17px;
    color: var(--white);
}

.tomato {
    font-size: 16px;
    line-height: 17px;
    color: var(--white);
}

.inProgress {
    background-color: var(--primary);
}

.break {
    background-color: var(--secondary);
}