.taskComponent {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--grey-lighter);
    padding: 15px 0;
    background-color: var(--white);
    cursor: grab;
}

.taskComponent:first-child {
    border-top: 1px solid var(--grey-lighter);
}

.taskContainer {
    display: flex;
    align-items: center;
    gap: 10px;
}

.number {
    padding: 0.4em 0 0 0.7em;
    border: 1px solid var(--grey-lighter);
    border-radius: 50%;
    width: 2em;
    height: 2em;
    font-size: 16px;
    font-weight: 300;
    line-height: 17px;
}